@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;500;700&display=swap");
:root {
  --system-black: #000000;
  --system-white: #FFFFFF;
  --system-gray: #CCCCCC;
  --system-charcoal: #5f5f5f;
  --brand-color-blue: #1A2B50;
  --brand-color-purple: #99AAFF;
  --brand-color-purple2: #7484D7;
  --brand-color-purple3: #3B3B90;
  --brand-color-purple4: #1E1E6A;
  --brand-color-grey: #EEEEEE;
  --brand-color-grey2: #EAEBF1;
  --colour-slogan-highlight: var(--brand-color-purple);
  --colour-social-links: var(--brand-color-purple3);
  --colour-social-background: var(--brand-color-purple);
  --colour-text: var(--color-black);
  --colour-headers: var(--brand-color-blue);
  --colour-links: var(--brand-color-purple2);
  --button-hamburger: var(--brand-color-purple3);
  --button-border-purple: var(--brand-color-purple3);
  --color-menu-chevrons: var(--brand-color-purple);
  --color-submit-button: var(--brand-color-purple2);
  --color-footer-bg: var(--brand-color-purple4);
  --color-pod-bg-1: var(--brand-color-grey2);
}

* {
  outline: none;
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: var(--colour-text);
  font-size: 16px;
  font-optical-sizing: auto;
  margin: 0;
  padding: 0;
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: normal;
  font-style: normal;
}

/* global link color */
a {
  color: var(--colour-links);
  text-decoration: none;
}
a:visited, a:active, a:hover {
  color: var(--colour-links);
  text-decoration: underline;
}

h1 {
  font-size: 1.75rem;
  line-height: 1;
  margin: 0;
}
@media (min-width: 992px) {
  h1 {
    font-size: 45px;
    padding: 0 0 1.675rem 0;
  }
}

h2 {
  line-height: 1.2;
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--colour-headers);
  padding: 0 0 1rem 0;
  margin: 0;
}
@media (min-width: 992px) {
  h2 {
    font-size: 2.25rem;
    padding: 0 0 1.625rem 0;
  }
}

h3 {
  margin: 0;
  font-size: 1.3rem;
  line-height: 110%;
  padding: 0 0 0.875rem 0;
}
@media (min-width: 992px) {
  h3 {
    font-size: 1.675rem;
    padding: 0 0 1.2rem 0;
  }
}

h4 {
  margin: 0;
  font-size: 1.1rem;
  line-height: 110%;
  padding: 0 0 1.2rem 0;
}
@media (min-width: 992px) {
  h4 {
    font-size: 1.2rem;
    padding: 0 0 1.2rem 0;
  }
}

h1 a:hover, h2 a:hover, h3 a:hover {
  text-decoration: none !important;
}

td,
th,
blockquote,
p {
  line-height: 130%;
  font-size: 1rem;
  margin: 0;
}
@media (min-width: 992px) {
  td,
th,
blockquote,
p {
    font-size: 1.125rem;
  }
}

p {
  padding-bottom: 1.5rem;
}

ul, ol {
  margin-left: 1rem;
  margin-bottom: 1.5rem;
}
ul li, ol li {
  line-height: 130%;
  font-size: 1rem;
  margin: 0;
}

.details ul, .details ol {
  margin-left: 1rem;
}
.details li {
  padding-bottom: 1rem;
}

dl {
  font-size: 1rem;
}

strong {
  font-weight: bold;
}

.form [type=submit] {
  display: inline-block;
  margin: 1rem 0;
}
@media (min-width: 992px) {
  .form [type=submit] {
    margin: 2rem 0;
  }
}
.form a {
  color: var(--button-border-purple);
  font-weight: bold;
}
.form textarea {
  border: 1px solid var(--system-black);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: inherit;
  outline: none;
  border-radius: 1px;
  background-color: white;
  color: var(--colour-text);
  padding: 0.75rem 1rem;
  resize: none;
  width: 100%;
}
.form select {
  border: 1px solid var(--system-black);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: inherit;
  outline: none;
  border-radius: 1px;
  background-image: linear-gradient(45deg, transparent 50%, gray 50%), linear-gradient(135deg, gray 50%, transparent 50%), linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
}
.form select:focus {
  background-image: linear-gradient(45deg, green 50%, transparent 50%), linear-gradient(135deg, transparent 50%, green 50%), linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 15px) 1em, calc(100% - 20px) 1em, calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
  border-color: green;
  outline: 0;
}
.form input[type=text], .form input[type=email], .form input[type=phone], .form input[type=tel], .form input[type=number], .form input[type=password] {
  border: 1px solid var(--colour-text);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: inherit;
  outline: none;
  border-radius: 1px;
  background-color: white;
  color: var(--colour-text);
  padding: 0.375rem 0.875rem;
}
.form .form-group {
  margin-bottom: 1.25rem;
}
.form .form-group label {
  line-height: 130%;
  font-size: 1rem;
  display: block;
  padding-bottom: 0.5rem;
}
@media (min-width: 992px) {
  .form .form-group label {
    font-size: 1.25rem;
  }
}
.form .form-group.checkbox-group {
  display: flex;
  align-items: flex-start;
  gap: 1.2rem;
  margin-top: 0.5rem;
}
.form .form-group.checkbox-group label {
  cursor: pointer;
  font-weight: bold;
}
.form .form-group.checkbox-group input {
  transform: scale(1.5);
  margin-top: 0.35rem;
}

html {
  scroll-behavior: smooth;
  background-color: white;
}

.container {
  /* 
  Adjusted to match design 1166 max-width for dektop/laptop devices
  Takes into account padding added by Bootstrap
  */
  max-width: 1198px;
}

section {
  padding: 2rem 0;
}
section.shaded {
  background-color: var(--brand-color-grey);
}
@media (min-width: 992px) {
  section {
    padding: 3rem 0;
  }
}
section.no-padding {
  padding-bottom: 0;
}

.text-large p {
  font-size: 1.275rem;
}

.mobile-only {
  display: none !important;
}

.desktop-only {
  display: block;
}

@media (max-width: 769px) {
  .mobile-only {
    display: block !important;
  }

  .desktop-only {
    display: none !important;
  }
}
.preload {
  display: none;
}

.small-print * {
  font-size: 0.95rem;
}

.pre-script {
  display: none !important;
}

/* ================================================================= */
a.button-link, button.button-link, span.button-link {
  --link-color: var(--brand-color-blue);
  border: 3px solid var(--link-color);
  text-decoration: none;
  line-height: 46px;
  width: 100%;
  white-space: nowrap;
  color: var(--link-color);
  padding: 0 1rem;
  padding-right: 50px;
  font-size: 20px;
  background-color: transparent;
  display: inline-block;
  border-radius: 10px;
  text-decoration: none;
  cursor: pointer;
  position: relative;
}
a.button-link.button-white, button.button-link.button-white, span.button-link.button-white {
  --link-color: var(--system-white);
}
a.button-link.button-filled, button.button-link.button-filled, span.button-link.button-filled {
  background-color: var(--brand-color-blue);
  border-color: var(--brand-color-blue);
}
a.button-link:after, button.button-link:after, span.button-link:after {
  font-weight: bold;
  display: inline-block;
  text-rendering: auto;
  font-family: "Font Awesome 6 Free";
  -webkit-font-smoothing: antialiased;
  content: "\f054";
  color: var(--link-color);
  position: absolute;
  right: 12px;
  top: 50%;
  font-size: 30px;
  transform: translateY(-50%);
}
a.button-link.button-padded, button.button-link.button-padded, span.button-link.button-padded {
  width: auto;
  padding-right: 7rem;
}
@media (max-width: 1200px) {
  a.button-link, button.button-link, span.button-link {
    width: auto;
  }
}
@media (hover: hover) {
  a.button-link:hover, button.button-link:hover, span.button-link:hover {
    color: var(--link-color);
  }
  a.button-link:hover:after, button.button-link:hover:after, span.button-link:hover:after {
    animation-name: twitch;
    animation-duration: 0.33s;
  }
}
@media (max-width: 576px) {
  a.button-link, button.button-link, span.button-link {
    width: 100% !important;
  }
}
@keyframes twitch {
  0% {
    right: 12px;
  }
  50% {
    right: 9px;
  }
  100% {
    right: 12px;
  }
}

/* ================================================================= */
button .hamburger-box span {
  transition: all 0.33s;
  background-color: var(--button-hamburger);
}
button .hamburger-box span:before, button .hamburger-box span:after {
  background-color: var(--button-hamburger);
}
@media (hover: hover) {
  button .hamburger-box:hover span {
    background-color: var(--button-hamburger);
  }
  button .hamburger-box:hover span:before, button .hamburger-box:hover span:after {
    background-color: var(--button-hamburger);
  }
}
button.is-active .hamburger-box span {
  background-color: var(--system-black);
}
button.is-active .hamburger-box span:before, button.is-active .hamburger-box span:after {
  background-color: var(--system-black);
}

/* ================================================================= */
.pod {
  background-color: var(--color-pod-bg-1);
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  min-height: 400px;
  margin-bottom: 2rem;
}
.pod .text-button {
  padding: 2.5rem;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.pod .details {
  padding-bottom: 1rem;
  flex: 1;
}
.pod .single-paragraph {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.pod .single-paragraph > p {
  margin: auto 0;
  padding: 0;
}
.pod .imagery {
  width: 50%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.pod.pod-white {
  background-color: var(--system-white);
}
.pod.pod-purple {
  background-color: var(--brand-color-purple4);
  color: var(--system-white);
}
.pod.background-pod {
  background-color: transparent;
  position: relative;
}
.pod.background-pod .imagery {
  position: absolute;
  inset: 0;
  width: 100%;
  z-index: -1;
}
.pod.background-pod .text-button {
  margin-top: auto;
}
@media (max-width: 576px) {
  .pod {
    flex-direction: column;
  }
  .pod .text-button {
    padding: 1.5rem;
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .pod .details {
    padding-bottom: 1rem;
    flex: 1;
  }
  .pod .imagery {
    width: auto;
    min-height: 270px;
  }
}

.editorial {
  max-width: 600px;
}
.editorial p {
  line-height: 140%;
}

.imagery {
  width: 100%;
  display: block;
}
.imagery.stock {
  border-radius: 10px;
}

.editorial + .button-link {
  margin-bottom: 50px;
}

.column-two {
  padding-bottom: 2rem;
}
@media (min-width: 992px) {
  .column-two {
    columns: 2;
    column-gap: 4rem;
  }
}

.column-three {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .column-three {
    columns: 2;
    column-gap: 3rem;
  }
}
@media (min-width: 992px) {
  .column-three {
    columns: 3;
    column-gap: 3rem;
  }
}

.external-link {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .external-link {
    padding-bottom: 2rem;
  }
}

/* ================================================================= */
/* .back-to-top */
.back-to-top {
  display: inline-block;
  width: 44px;
  height: 44px;
  text-align: center;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}
@media (max-width: 992px) {
  .back-to-top {
    display: none;
  }
}
.back-to-top:after {
  content: "\f077";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  font-size: 1.6em;
  line-height: 44px;
}
@media (hover: hover) {
  .back-to-top:hover {
    cursor: pointer;
    text-decoration: none;
    color: black;
    opacity: 1;
  }
}
.back-to-top.show {
  opacity: 1;
  visibility: visible;
}
@media (min-width: 992px) {
  .back-to-top {
    width: 50px;
    height: 50px;
  }
  .back-to-top:after {
    font-size: 2em;
    line-height: 50px;
  }
}

/* ================================================================= */
/* ================================================================= */
.spacer {
  height: var(--height);
}

.table {
  margin-bottom: 2rem;
  width: 100%;
}
@media (min-width: 992px) {
  .table {
    width: auto;
  }
}
.table th {
  padding-left: 0;
  padding-top: 0;
  padding-bottom: 0.75rem;
}
@media (min-width: 992px) {
  .table th {
    padding-right: 3rem;
  }
}
.table td {
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  padding-bottom: 0.75rem;
  text-align: right;
  font-weight: bold;
}

/* ================================================================= */
/* Header */
/* ================================================================= */
.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 1rem 0;
}
.header .logo-slogan {
  display: flex;
  justify-content: flex-start;
  gap: 2rem;
}
.header .logo {
  display: block;
}
.header .logo img {
  display: block;
  width: 123px;
  aspect-ratio: 123/118;
}
.header .slogan {
  font-weight: 200;
  font-size: 26px;
  max-width: 228px;
}
.header .slogan span {
  color: var(--colour-slogan-highlight);
  font-style: italic;
}
.header .slogan p {
  line-height: 120%;
  font-size: 26px;
}
.header .social-search-buttton {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
}
.header .social-search-buttton > button {
  margin-top: 20px;
}
.header .social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 0.375rem;
}
.header .social a {
  text-decoration: none;
}
.header .search {
  border-radius: 10px;
  border: solid 2px var(--system-gray);
  padding: 2px;
}
.header .search input {
  padding: 8px 12px;
  border-radius: 10px;
  border: none;
  width: 242px;
}
.header .search input::placeholder {
  opacity: 0.5;
}
.header .search button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: var(--color-submit-button);
  padding: 6px;
  border: none;
}
.header .search button i {
  color: var(--color-submit-button);
}
.header .icon {
  margin-right: 5px;
}
.header .icon i {
  font-size: 30px;
  color: var(--colour-social-background);
}
.header .careers-specific {
  width: 282px;
  font-weight: normal;
}
.header .button {
  color: var(--colour-social-links);
  background-color: var(--colour-social-background);
  font-size: 16px;
  font-weight: bold;
  display: block;
  padding: 0 16px;
  line-height: 46px;
  border-radius: 10px;
}
.header .social {
  --extra-padding: 45px;
}
.header .social li:nth-last-child(2) .button {
  padding-right: var(--extra-padding);
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.header .social li:last-child .button {
  padding-left: var(--extra-padding);
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.header .mobile-nav-trigger {
  display: none;
}
@media (max-width: 992px) {
  .header .logo img {
    width: 80px;
  }
  .header .social, .header .slogan, .header .search {
    display: none;
  }
  .header .mobile-nav-trigger {
    display: block;
  }
}

/* Navigation/Menu */
/* ================================================================= */
.global-navigation {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.global-navigation a {
  color: var(--color-black);
  font-size: 20px;
  font-weight: normal;
  line-height: 100%;
  padding: 18px 12px;
  display: block;
  white-space: nowrap;
}
.global-navigation a:hover {
  text-decoration: underline;
}
.global-navigation li.active > a {
  text-decoration: underline;
}
.global-navigation > nav > ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.global-navigation > nav > ul > li {
  display: inline-block;
  padding: 0;
  position: relative;
  transition: all 0.33s;
}
.global-navigation > nav > ul > li:hover {
  background-color: white;
}
.global-navigation > nav > ul > li:hover > a {
  text-decoration: underline;
}
.global-navigation > nav > ul > li:hover ul {
  display: block;
}
.global-navigation > nav > ul > li i {
  position: absolute;
  top: 20px;
  right: 9px;
  color: var(--color-menu-chevrons);
  transform: scale(0.9);
}
.global-navigation > nav > ul > li:has(ul) > a {
  padding-right: 30px;
}
.global-navigation > nav > ul ul {
  position: absolute;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: white;
  display: none;
  z-index: 1;
}
.global-navigation > nav > ul ul li {
  border-bottom: 1px solid rgba(128, 128, 128, 0.1);
}
.global-navigation > nav > ul ul li:last-child {
  border-bottom: none;
}
.global-navigation > nav > ul ul li.active a {
  cursor: default;
}
.global-navigation > nav > ul ul a {
  font-size: 18px;
  padding: 8px 60px 8px 12px;
}
.global-navigation .mobile-only {
  display: none;
}
@media (max-width: 992px) {
  .global-navigation {
    display: none;
  }
}
.global-navigation .mobile-button {
  display: none;
}

/* Carousel */
/* ================================================================= */
.carousel-main {
  --slideHeight: 566px;
}
.carousel-main:has(.preload) {
  height: 566px;
}
.carousel-main .preload {
  display: none;
}
.carousel-main .slide {
  width: 100%;
  height: var(--slideHeight);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.carousel-main .layout {
  height: calc(var(--slideHeight) - 80px);
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.carousel-main .box {
  background-color: var(--system-white);
  padding: 30px 35px;
  width: 467px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.15);
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.15);
}
@media (max-width: 992px) {
  .carousel-main .slide {
    height: 520px;
  }
  .carousel-main .layout {
    height: 504px;
  }
}

.slick-dots {
  position: absolute;
  bottom: 0.875rem;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  bottom: 1rem;
}
.slick-dots li {
  margin: 0 0.675rem;
  padding: 0;
}
.slick-dots li button {
  text-indent: -9000px;
  width: 1.35rem;
  height: 1.35rem;
  border-radius: 50%;
  border: 3px solid var(--system-white);
  background-color: var(--system-white);
  transition: all 0.25s;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  -webkit-appearance: none;
  padding: 0;
}
.slick-dots li.slick-active button {
  background-color: transparent;
  cursor: default;
}
@media (max-width: 992px) {
  .slick-dots {
    position: static;
    margin: 1rem 0;
  }
  .slick-dots li {
    margin: 0 0.375rem;
  }
  .slick-dots li button {
    border: 1px solid var(--system-charcoal);
    background-color: var(--system-charcoal);
    width: 0.675rem;
    height: 0.675rem;
  }
}

.hero-main {
  height: 360px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (max-width: 992px) {
  .hero-main {
    height: 220px;
  }
}

/* Footer */
/* ================================================================= */
.footer {
  background-color: var(--color-footer-bg);
  padding: 3rem 0;
}
.footer .logo-lists {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
}
.footer .logo {
  width: 90px;
}
.footer .logo img {
  display: block;
  width: 100%;
  margin-left: 1rem;
}
.footer .lists {
  display: flex;
  flex-direction: column;
}
.footer .list a {
  color: var(--system-white);
  opacity: 0.5;
  transition: all 0.33s;
  font-weight: normal;
  text-decoration: none;
}
@media (hover: hover) {
  .footer .list a:hover {
    opacity: 1;
  }
}
.footer ul {
  margin: 0 0 3rem 0;
  padding: 0 1rem 0 2rem;
  list-style: none;
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer ul li.active a {
  opacity: 1;
}
@media (min-width: 992px) {
  .footer .logo-lists {
    gap: 4rem;
  }
  .footer .logo {
    width: 123px;
  }
  .footer .logo img {
    margin: 0;
  }
  .footer .lists {
    flex-direction: row;
    flex: 1;
  }
  .footer .lists > div:last-child {
    margin-left: auto;
  }
  .footer .list {
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    padding: 0 0 40px 40px;
  }
  .footer ul {
    margin: 0;
    padding: 0;
    border: none;
  }
  .footer ul li {
    padding: 0 0 10px 0;
  }
}

/* Groups */
/* ================================================================= */
.groups .group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  padding-bottom: 2rem;
}
.groups .group .imagery {
  max-width: 400px;
}
@media (min-width: 992px) {
  .groups {
    display: flex;
    gap: 2rem;
    align-items: flex-start;
  }
  .groups .group {
    display: flex;
    flex-direction: column;
    align-items: initial;
    gap: 2rem;
    padding-bottom: 0;
  }
  .groups .group .imagery {
    margin-bottom: 1rem;
  }
  .groups .height-adjust {
    height: 300px;
    background-color: red;
    position: relative;
  }
  .groups .height-adjust img {
    max-height: 300px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .groups .details {
    min-height: 240px;
  }
}
@media (min-width: 1200px) {
  .groups {
    gap: 6rem;
  }
}

/* Memberships */
/* ================================================================= */
.memberships {
  margin-bottom: 2rem;
  background-color: var(--system-white);
  padding: 40px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.memberships > div {
  margin: 0 auto;
}
.memberships img {
  max-width: 300px;
}
.memberships > div:nth-child(2) {
  border-top: 1px solid var(--system-gray);
  padding-top: 2rem;
  border-bottom: 1px solid var(--system-gray);
  padding-bottom: 2rem;
}
@media (min-width: 992px) {
  .memberships {
    padding: 40px 0;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    gap: 0;
  }
  .memberships > div {
    padding: 0 60px;
    display: flex;
  }
  .memberships > div:nth-child(2) {
    border: none;
    border-left: 1px solid var(--system-gray);
    border-right: 1px solid var(--system-gray);
  }
  .memberships img {
    max-width: 280px;
  }
}

/* Lists - Documents + News */
/* ================================================================= */
.items-list {
  display: flex;
  flex-direction: column;
}
.items-list > div:nth-child(even) {
  background-color: var(--system-white);
}
.items-list > div:first-child .article {
  padding-top: 0;
}
.items-list .article {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px 12px 20px 12px;
}
.items-list .article-details img {
  max-width: 500px;
  width: 100%;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .items-list > div .article-details {
    border-left: 2px solid var(--system-white);
  }
  .items-list > div:nth-child(even) {
    background-color: var(--system-white);
  }
  .items-list > div:nth-child(even) .article-details {
    border-left: 2px solid var(--brand-color-grey);
  }
  .items-list > div:first-child .article {
    padding-top: 0;
  }
  .items-list .article-details {
    margin: 20px 0;
    padding-left: 40px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    gap: 4rem;
  }
  .items-list .article-details .details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .items-list .article-details .text {
    flex: 1;
  }
  .items-list .article-details img {
    width: 300px;
    margin: 0;
  }
}

/* Pagination */
/* ================================================================= */
.pagination {
  --link-color: var(--brand-color-blue);
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.pagination ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.pagination li {
  display: inline-block;
  padding: 3px;
}
.pagination li.active a {
  background-color: var(--brand-color-grey);
  border-color: var(--system-gray);
  cursor: default;
}
.pagination li.disabled a {
  border: 2px solid var(--brand-color-grey);
  color: var(--brand-color-grey);
  cursor: default;
}
.pagination a {
  display: inline-block;
  padding: 10px 16px;
  min-width: 46px;
  text-align: center;
  border-radius: 8px;
  color: var(--link-color);
  text-decoration: none;
  border: 2px solid var(--link-color);
  font-weight: bold;
  transition: all 250ms;
}
@media (hover: hover) {
  .pagination a:hover {
    color: var(--color-footer-bg);
  }
}
@media (max-width: 992px) {
  .pagination {
    padding: 20px 0 0 0;
    justify-content: center;
  }
}

/* Members grid */
/* ================================================================= */
.members-grid {
  --border-color: var(--system-gray);
}
.members-grid .item {
  padding: 0 0 30px 0;
}
.members-grid a {
  display: block;
}
.members-grid img {
  width: 100%;
  display: block;
}
@media (min-width: 768px) {
  .members-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 3rem;
    margin-bottom: 4rem;
  }
  .members-grid .item {
    display: flex;
    height: 280px;
    padding: 0;
  }
  .members-grid a {
    display: inline-block;
    max-width: 300px;
    max-height: 300px;
    margin: auto;
  }
  .members-grid > div:nth-child(odd) {
    border-right: 1px solid var(--border-color);
  }
  .members-grid.odd > div:last-child {
    border-right: none;
  }
}
@media (min-width: 1200px) {
  .members-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .members-grid > div:nth-child(odd) {
    border-right: unset;
  }
  .members-grid > div:nth-child(3n+2) {
    border-left: 1px solid var(--border-color);
    border-right: 1px solid var(--border-color);
  }
  .members-grid.last-item-center > div:last-child {
    border-right: none;
  }
}

/* SlideOut Menu */
/* ================================================================= */
.mm-panels {
  --colour-links: var(--system-charcoal);
}
.mm-panels .mm-panel {
  background-color: white;
}
.mm-panels a {
  font-size: 1.2rem;
  color: var(--colour-links) !important;
}
.mm-panels .mm-btn:after, .mm-panels .mm-btn:before {
  border-color: var(--colour-links) !important;
}
.mm-panels .active a {
  font-weight: bold;
  color: var(--brand-color-blue) !important;
}
.mm-panels > div:first-child .mm-navbar_sticky {
  display: none;
}
.mm-panels .mm-navbar_sticky > a:last-child {
  opacity: 0;
}

/* SlideOut Menu */
/* ================================================================= */
.organogram {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .organogram {
    padding-bottom: 2rem;
  }
}

/* Member */
/* ================================================================= */
.logo-details {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .logo-details {
    flex-direction: row;
    align-items: center;
    margin-bottom: 2rem;
  }
  .logo-details .logo {
    width: 350px;
  }
  .logo-details .imagery {
    width: 300px;
  }
  .logo-details .details {
    flex: 1;
    padding: 1.5rem 2rem 0 5rem;
    border-left: 2px solid var(--brand-color-grey);
  }
}

.lightbox-information {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.lightbox-information .lightbox {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .lightbox-information {
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 2rem;
  }
  .lightbox-information .lightbox {
    width: 350px;
    padding-bottom: 2rem;
    padding-top: 2rem;
  }
  .lightbox-information .information {
    flex: 1;
    padding: 2rem 2rem 0 5rem;
    border-left: 2px solid var(--system-white);
  }
}

/* Gallery */
/* ================================================================= */
.gallery {
  display: flex;
  overflow: scroll;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
.gallery a img {
  max-width: 300px;
  width: unset;
  aspect-ratio: 490/470;
}
.gallery:-webkit-scrollbar {
  display: none;
}
@media (min-width: 768px) {
  .gallery {
    gap: 0.5rem;
    display: grid;
    grid-template-columns: 110px 110px;
    overflow: unset;
    justify-content: flex-start;
  }
  .gallery a {
    display: block;
    border: 1px solid var(--system-white);
  }
  .gallery a img {
    max-width: unset;
    width: 100%;
    display: block;
  }
}
@media (min-width: 768px) and (hover: hover) {
  .gallery a:hover {
    border-color: var(--link-color);
  }
}

/* products */
/* ================================================================= */
.product-items {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 1rem;
}
.product-items .title {
  padding-bottom: 1rem;
}
.product-items .product-item {
  background-color: var(--color-pod-bg-1);
  border-radius: 10px;
  overflow: hidden;
}
.product-items .title-intro {
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
}
.product-items .intro * {
  margin: 0;
  padding: 0;
}
.product-items .imagery {
  height: 240px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.product-items .text-button {
  position: absolute;
  padding: 2rem;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
}
.product-items .text-button .button-link:after {
  content: "\f5fd";
  font-size: 28px;
}
.product-items .details {
  padding: 20px 30px;
}
@media (min-width: 768px) {
  .product-items .details ul {
    columns: 2;
    column-gap: 4rem;
    margin-bottom: 0;
  }
}
@media (min-width: 992px) {
  .product-items .details ul {
    columns: 3;
  }
}
@media (max-width: 1200px) {
  .product-items .product-item {
    display: none;
  }
  .product-items .product-item.narrow-selected {
    display: block;
  }
}
@media (min-width: 1201px) {
  .product-items {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    padding-bottom: 2rem;
  }
  .product-items:has(.wide-selected) .product-item {
    display: none;
  }
  .product-items:has(.wide-selected) .product-item.wide-selected {
    display: block;
  }
  .product-items .title-intro {
    height: 160px;
    overflow: hidden;
  }
  .product-items .title {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .product-items .imagery-details {
    position: relative;
    height: 460px;
  }
  .product-items .imagery {
    position: absolute;
    inset: 0;
    height: unset;
  }
  .product-items .text-button {
    display: block;
  }
  .product-items .details {
    position: absolute;
    inset: 0;
    top: -94px;
    background-color: var(--color-pod-bg-1);
    padding-top: 0;
    opacity: 0;
    transition: opacity 0.33s;
  }
  .product-items .details:hover {
    opacity: 1;
  }
  .product-items .details ul {
    columns: unset;
  }
}

@media (min-width: 1201px) {
  .mobile-filter {
    display: none;
  }
}
.mobile-filter select {
  height: 50px;
  font-size: 16px;
  text-transform: uppercase;
  width: 100%;
  padding: 0 1rem;
  border: 3px solid var(--brand-color-blue);
  color: var(--brand-color-blue);
  border-radius: 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  padding-right: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mobile-filter .mobile-switcher {
  max-width: 400px;
  position: relative;
  margin-bottom: 1.5rem;
}
.mobile-filter .mobile-switcher:after {
  font-weight: bold;
  display: inline-block;
  text-rendering: auto;
  font-family: "Font Awesome 6 Free";
  -webkit-font-smoothing: antialiased;
  content: "\f054";
  color: var(--link-color);
  position: absolute;
  right: 18px;
  top: 50%;
  font-size: 20px;
  transform: translateY(-50%) rotate(90deg);
}

.desktop-filter {
  display: none;
}
@media (min-width: 1200px) {
  .desktop-filter {
    display: block;
  }
}
.desktop-filter ul {
  list-style: none;
  margin: 0 0 50px 0;
  padding: 0;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.desktop-filter button {
  padding-right: 60px;
  transition: all 0.33s;
}
.desktop-filter button:after {
  font-size: 20px;
  transition: all 0.33s;
}
.desktop-filter button.active {
  background-color: var(--brand-color-purple);
}
@media (hover: hover) {
  .desktop-filter button.active:hover:after {
    animation-name: none;
  }
}
.desktop-filter button.active:after {
  transform: translateY(-50%) rotate(90deg);
}

/* industry facts */
/* ================================================================= */
.industry-facts {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
.industry-facts .industry-fact {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  background-color: var(--brand-color-grey);
}
.industry-facts .industry-fact .details {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 2rem;
  gap: 1rem;
  text-align: center;
}
.industry-facts .industry-fact .details > * {
  line-height: 1.2;
  margin: 0;
  padding: 0;
}
@media (min-width: 576px) {
  .industry-facts {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .industry-facts {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 992px) {
  .industry-facts {
    gap: 2rem;
  }
}